.sales-con {
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  // height: 635px;
  background-color: #fff;
  .sales-view {
    width: 50%;
    padding: 20px;
    .sales-header {
      margin-bottom: 19px;
      .sales-line {
        display: inline-block;
        width: 4px;
        height: 20px;
        margin-right: 8px;
        vertical-align: middle;
        background-color: #217ba0;
        border-radius: 4px;
      }
      .sales-text {
        color: #000;
        font-size: 14px;
        vertical-align: middle;
      }
    }

    .sales-ranking {
      text-align: center;
      .sales-ranking-list {
        display: inline-block;
        width: 26px;
        height: 26px;
        color: #fff;
        line-height: 26px;
        text-align: center;
        background-image: linear-gradient(136deg, #ffeca0 0%, #ff8888 100%);
        border-radius: 50px;
      }
    }
    .sales-ranking-font {
      font-size: 16px;
    }
  }
  .sales-left {
  }
  .sales-right {
  }
}
