.revenue-con {
  margin-bottom: 12px;
  .revenue-view {
    display: flex;
    justify-content: space-between;
    .revenue-list {
      width: 100%;
      margin-right: 12px;
      padding: 20px;
      color: #fff;
      background-color: #ddd;
      border-radius: 4px;
      box-shadow: 0 1px 4px 0 rgba(0, 21, 41, 0.12);
      // height: 200px;

      &:last-child {
        margin-right: 0px;
      }
      .list-header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 24px;
        .header-left {
          font-size: 16px;
        }
        .header-right {
          padding: 5px 12px;
          color: #b38000;
          background: #ffffff;
          border-radius: 25px;
          cursor: pointer;
        }
        .header-icon {
          padding: 10px;
        }
        .header-prop {
          .prop-title {
            .prop-line {
              width: 4px;
              height: 14px;
              background-color: #f8c33d;
            }
          }
        }
      }

      .list-money {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 93px;
        padding-bottom: 22px;
        font-size: 40px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.3);
        .money-decimal {
          font-size: 20px;
        }
      }

      .list-total {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 15px;
        .total-money {
          font-size: 20px;
        }
      }
    }
    .bg-yellow {
      background-color: #f8c33d;
    }
    .bg-order {
      background-color: #46a0c0;
    }
    .bg-service {
      background-color: #46c08c;
    }
    .bg-add {
      background-color: #4688c0;
    }
  }
}
