.prop-con {
  width: 347px;
  .prop-title {
    margin-bottom: 19px;
    .prop-line {
      display: inline-block;
      width: 4px;
      height: 14px;
      margin-right: 8px;
      vertical-align: middle;
      background-color: #f8c33d;
      border-radius: 4px;
    }
    .prop-text {
      color: #000;
      font-size: 14px;
      vertical-align: middle;
    }
  }
  .prop-bottom {
    .prop-list {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 12px;
      .prop-label {
        color: #b38000;
        font-size: 14px;
      }
      .prop-money {
        color: #000;
        font-size: 20px;
      }
    }
  }
}
